import React from 'react';
import Container from '../Container/Container';
const VR = () => {
    return (
        <Container>
            <h1>Vn Trc Ch</h1>
            <p className='text-txt2 font-medium text-[28px] md:text-[32px] max-w-[80%] m-auto py-[36px]'>Cng m chm vo khng gian ngh thut ca Trc Ch Garden vi cng ngh thc t o 360 VR.</p>
            <iframe
                id="iframe_tour_vr"
                src="https://refs.sgallery.vn/ndc/vrtour/"
                className="min-h-[100vh] max-w-[1440px] w-full m-auto px-2"
                title="iframe-vr-tour"
            />
        </Container>
    );
};



export default VR;
