import React from "react";
import banner from "../assets/images/about-banner.png";
import Container from "../components/Container/Container";
import { ReactComponent as IconLine } from "../assets/icon/line.svg";
import historyImg from "../assets/images/history.png";

const Banner = () => {
  return (
    <Container>
      <img
        src={banner}
        alt=""
        className="absolute inset-0 object-cover w-full h-full"
        style={{
          background: `linear-gradient(360deg, #384E4D 0%, rgba(56, 78, 77, 0) 100%)`
        }}
      />
    </Container>
  );
};

const Introduce = () => {
  return (
    <Container className="mt-[-400px] min-h-0">
      <div className="max-w-[80%] m-auto">
        <h1 className="w-full m-0 my-[95px] ml-auto text-right">
          Khi nim Trc Ch
        </h1>
        <div className="flex gap-[20px]">
          <h4 className="flex-1 text-left">
            Trc Ch l tn gi vn hoa ca mt loi Giy - ngh - thut, Ngh -
            thut - giy mi ca Vit Nam da trn c s ngh giy truyn thng;
            vi hm  Tre trc l biu tng ca Vn ha v tinh thn Vit.
          </h4>
          <h4 className="flex-1 text-left">
            Trc Ch l kt qu ca cng trnh khoa hc do Ha s Phan Hi Bng
            cng cc cng s nghin cu v pht trin t nm 2000 n nay.
          </h4>
        </div>
      </div>
      <IconLine className="mt-[125px] m-auto max-w-[80%]" />
    </Container>
  );
};

const History = () => {
  return (
    <Container className="min-h-0">
      <div className="max-w-[80%] m-auto flex">
        <h2 className="w-full m-0 ml-auto text-left">
          Lch s hnh thnh v pht trin Trc Ch
        </h2>
        <img src={historyImg} alt="" className="max-h-[455px]" />
      </div>
    </Container>
  );
};

const CircleInfo = () => {
  return (
    <Container className="min-h-0">
      <div className="max-w-[80%] m-auto flex gap-[38px]">
        <div className="circle rounded-full h-[466px] w-[466px] bg-txt1"></div>
        <div className="flex items-center justify-center flex-1">
          <h4 className="text-right">
            Trong khong thi gian sau i mi, gii ha s  ha Vit Nam lun
            thiu thn giy c chng  hon thin tc phm t cc k thut
            cht liu in n, ngoi loi giy c tn thng gi l xp c v
            mt s loi giy thng thng khc. Mt khc, cc ha s cng kht
            khao tm kim nhng biu hin khc na ca giy - nn cho cc bn
            in.
          </h4>
        </div>
      </div>
    </Container>
  );
};

const News = () => {
  const data = [
    {
      imgSrc: "",
      title: "Nm 2000",
      content:
        "Ha s  ha Phan Hi Bng (ging vin i hc Ngh thut, i hc Hu)  cng cc cng s lin tc th nghim, tm ti cch to nn loi giy c chng cho cc k thut cht liu in n."
    },
    {
      imgSrc: "",
      title: "Thng 04/2012",
      content:
        "Ngh - thut Giy, Giy - ngh - thut mi ny c Nh vn, Dch gi Bu  lp danh l Trc Ch vi  ngha hnh tng cy tre/trc l biu tng vn ha v tinh thn Vit."
    },
    {
      imgSrc: "",
      title: "Thng 09/2013",
      content: "Cng ty TNHH Ngh thut Trc Ch Vit Nam chnh thc thnh lp."
    }
  ];
  return (
    <Container className="min-h-0">
        {data.map((item,idx) => (
          <div className="flex max-w-[80%] m-auto mb-[72px]">
            <div className="flex-1">
              <img
                src={item.imgSrc}
                alt=""
                className="object-cover h-[521px] max-w-[549px] w-full bg-txt1"
              />
            </div>
            <div className="flex-1 flex flex-col gap-[45px]">
              <h2>{item.title}</h2>
              <h4 className="text-left">{item.content}</h4>
            </div>
          </div>
        ))}
        <button className="m-auto">c tip</button>
    </Container>
  );
};

const AboutUs = () => {
  return (
    <>
      <Banner />
      <Introduce />
      <History />
      <CircleInfo />
      <News />
    </>
  );
};

export default AboutUs;
