import { Card } from 'antd';
import React from 'react';
import Carousel from '../Carousel';
import Container from '../Container/Container';

const News = () => {
    const data = [
        {
            imgSrc:"./assets/images/news-1.png",
            content:"Ngi khai sinh ngh thut Trc ch Vit Nam"
        },
        {
            imgSrc:"./assets/images/news-2.png",
            content:"c o ngh thut Trc ch Vit Nam"
        },
    ]

    const setting = {
        slidesToShow: 1,
        responsive:[],
        arrows:true
    }
    return (
        <Container>
        <div className="max-w-[90%] m-auto">
          <h1>Ngh thut Trc Ch</h1>
          <p className="px-[35px] py-[40px]">Trc Ch l Ngh - thut - giy, Giy  ngh - thut ca Vit Nam, mt Gi tr Vit mi c im khi u v qu hng ti C  Hu.</p>
          <Carousel setting={setting}>
            {data.map((item, idx) => (
              <div key={idx} className="px-[35px]">
                <Card
                  className="border-0 max-h-[875px] bg-primaryBg w-full max-w-[1024px] m-auto "
                  hoverable
                  cover={
                    <img
                      alt="example"
                      src={item.imgSrc}
                    />
                  }>
                    <p className='justify-center'>{item.content}</p>
                    <button className="m-auto my-[40px]">Xem thm</button>
                </Card>
              </div>
            ))}
          </Carousel>
        </div>
      </Container>
    );
}

export default News;
