import React from "react";
import Container from "../Container/Container";
import imgDefinition from "../../assets/images/definition.png";
const Definition = () => {
  return (
    <Container background={imgDefinition}>
      <img
        src={imgDefinition}
        alt=""
        className="absolute inset-0 z-[0] w-full h-full"
      />
      <div className="flex justify-between flex-1 relative z-[1] h-[100vh] items-center md:p-[75px] p-[35px]">
        <div className="flex-1 hidden md:block"></div>
        <div className="flex-1">
          <h2 className="py-[70px]">S mnh</h2>
          <p>
            {`Mang li cho Giy thm kh nng, thot khi thn \nphn lm nn  tr thnh tc phm t thn, \nc lp.`}
          </p>
          <button className="m-auto my-[65px]">Xem thm</button>
        </div>
      </div>
    </Container>
  );
};

export default Definition;
